<!-- 标题设置 -->
<template>
  <!-- 添加图表 -->
  <Modal :loading="loading" v-model="show" title="添加图表" :width="1000">
    <div slot="footer">
      <Button type="text" size="large" @click="onCancel">取消</Button>
      <Button type="primary" size="large" :loading="showLoading" @click="onOk">确定</Button>
    </div>
    <Tabs value="bar" class="chart-modal-content">
      <tab-pane
        :label="categories.label"
        :name="categories.name"
        v-for="(categories, index) of chartTypeList"
        :key="index"
      >
        <Row justify="center">
          <i-col
            span="5"
            offset="1"
            v-for="(item, index) of categories.typeList"
            :key="index"
            :class="item.allowed ? '' : 'no-allowed'"
            style="margin-top: 20px"
          >
            <div
              style="border: solid 1px #dcdee2; width: 180px; height: 130px"
              :class="activeId == item.id ? 'chart-selected' : ''"
              @click="selected(item)"
            >
              <img
                :src="'https://bootapi.jeecg.com/' + item.imgUrl"
                style="width: 95%; height: 95%; margin: 0 5px"
              />
              <span
                style="
                  float: left;
                  width: 180px;
                  margin-top: 8px;
                  text-align: center;
                  font-size: 12px;
                "
                >{{ item.name }}</span
              >
            </div>
          </i-col>
        </Row>
      </tab-pane>
    </Tabs>
  </Modal>
</template>
<script>
import chartTypeList from "@/data/chartTypeList";
export default {
  props: {
    loading: {
      type: Boolean,
      default: true,
    },
    showModal: {
      type: Boolean,
      default: false,
    },
    selectedId: {
      type: String,
      default: null,
    },
  },
  watch: {
    selectedId: {
      immediate: true,
      handler: function (val) {
        this.activeId = val;
      },
    },
    showModal: {
      immediate: true,
      handler: function (val) {
        this.show = val;
        val == true && (this.showLoading = false);
      },
    }
  },
  methods: {
    onOk: function () {
      if (this.activeType != null) {
        this.showLoading = this.loading;
        this.$emit("on-ok", this.activeType);
      } else {
        this.$Message.info('请选择图标类型');
      }
    },
    onCancel: function () {
      this.show = false;
      this.$emit("on-cancel");
    },
    selected: function (item) {
      if (item.allowed) {
        this.activeId = item.id;
        this.activeType = item;
        this.$emit("selected", item);
      }
    },
  },
  data() {
    return {
      chartTypeList,
      show: false,
      activeId: null,
      activeType: null,
      showLoading: false
    };
  },
};
</script>
