<!-- 数值设置 -->
<template>
  <Submenu name="15" style="border-bottom: inset 1px">
    <template slot="title"> 数值设置 </template>
    <div class="blockDiv" style="padding-bottom: 10px">
      <Row class="ivurow">
        <p class="config-item-title">显示&nbsp;&nbsp;</p>
        <i-switch
          size="small"
          style="margin-left: 105px"
          v-model="seriesOption.show"
          @on-change="onSeriesLabelChange"
        />
      </Row>
      <Row class="ivurow">
        <p class="config-item-title">字体大小&nbsp;&nbsp;</p>
        <i-input
          size="small"
          type="number"
          v-model="seriesOption.textStyle_fontSize"
          @on-blur="onSeriesLabelChange"
          style="width: 111px"
        ></i-input>
      </Row>
      <Row
        class="ivurow"
        v-if="typeof seriesOption.textStyle_color !== 'undefined'"
      >
        <p class="config-item-title">字体颜色&nbsp;&nbsp;</p>
        <Col>
          <i-input
            v-model="seriesOption.textStyle_color"
            size="small"
            style="width: 111px"
            @on-change="onSeriesLabelChange"
          >
            <span slot="append">
              <color-picker
                class="colorPicker"
                v-model="seriesOption.textStyle_color"
                :editable="false"
                :transfer="true"
                size="small"
                @on-change="onSeriesLabelChange"
              />
            </span>
          </i-input>
        </Col>
      </Row>
      <Row class="ivurow">
        <p class="config-item-title">字体粗细&nbsp;&nbsp;</p>
        <i-select
          size="small"
          class="iSelect"
          v-model="seriesOption.textStyle_fontWeight"
          @on-change="onSeriesLabelChange"
        >
          <i-option value="normal">normal</i-option>
          <i-option value="bold">bold</i-option>
          <i-option value="bolder">bolder</i-option>
          <i-option value="lighter">lighter</i-option>
        </i-select>
      </Row>
      <Row class="ivurow">
        <p class="config-item-title" style="margin-bottom: 10px">字体位置&nbsp;&nbsp;</p>
        <i-select
          size="small"
          class="iSelect"
          v-model="seriesOption.position"
          @on-change="onSeriesLabelChange"
        >
          <i-option
            v-for="(item, index) in seriesOption.labelPositionArray"
            :key="index"
            :value="item.value"
            :index="index"
          >
            {{ item.text }}
          </i-option>
        </i-select>
      </Row>
    </div>
  </Submenu>
</template>
<script>
export default {
  name: "chart-series-setting",
  props: {
    settings: {
      type: Object,
      required: true,
      default: () => {},
    },
  },
  data() {
    return {
      seriesOption: {
        textStyle_color: "",
      },
      labelPositions: [],
    };
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.initData();
      },
    },
  },
  methods: {
    initData: function () {
      if (this.settings) {
        this.seriesOption = Object.assign(this.seriesOption, this.settings);
      }
    },
    onSeriesLabelChange() {
      let { labelPositionArray, ...otherOptions } = this.seriesOption;
      this.$emit("change", otherOptions, "label");
    },
  },
};
</script>
<style scoped>
</style>