<template>
  <div style="height: auto; overflow: hidden; padding-bottom: 300px">
    <i-form label-position="top" class="newtec-form-barcode">
      <form-item label="二维码内容:">
        <i-input
          v-model="text"
          type="textarea"
          :rows="2"
          @on-blur="onCodeChange"
        ></i-input>
      </form-item>

      <form-item label="宽度:">
        <i-input
          v-model="width"
          placeholder="请输入宽度"
          :min="1"
          type="number"
          @on-blur="onCodeChange"
        ></i-input>
      </form-item>

      <form-item label="高度:">
        <i-input
          v-model="height"
          placeholder="请输入高度"
          :min="1"
          type="number"
          @on-blur="onCodeChange"
        ></i-input>
      </form-item>

      <form-item label="前景色:">
        <i-input
          v-model="colorDark"
          size="small"
          style="width: 111px"
          @on-change="onCodeChange"
        >
          <span slot="append">
            <color-picker
              class="colorDark"
              v-model="colorLight"
              :editable="false"
              :transfer="true"
              size="small"
              @on-change="onCodeChange"
            />
          </span>
        </i-input>
      </form-item>

      <form-item label="背景色:">
        <i-input
          v-model="colorLight"
          size="small"
          style="width: 111px"
          @on-change="onCodeChange"
        >
          <span slot="append">
            <color-picker
              class="colorPicker"
              v-model="colorLight"
              :editable="false"
              :transfer="true"
              size="small"
              @on-change="onCodeChange"
            />
          </span>
        </i-input>
      </form-item>
    </i-form>
  </div>
</template>
<script>
export default {
  name: "qr-code-setting",
  props: {
    settings: {
      type: Object,
      required: true,
      default: () => {
        return {
          text: "广州粤信科技有限公司",
          width: 128,
          height: 128,
          colorDark: "#000000",
          colorLight: "#ffffff"
        };
      },
    },
  },
  data() {
    return {
      text: "",
      width: "",
      height: "",
      colorLight: "#fff",
      colorDark: "#000",
    };
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.resetForm();
      },
    },
  },
  methods: {
    resetForm: function () {
      if (this.settings) {
        Object.keys(this.settings).map((k) => {
          this[k] = this.settings[k];
        });
      }
    },
    onCodeChange() {
      let obj = {};
      Object.keys(this.settings).map((k) => {
        obj[k] = this[k];
      });
      //清除颜色后恢复默认
      obj.colorLight = this.colorLight ? obj.colorLight : "#ffffff";
      obj.colorDark = this.colorDark ? this.colorDark : "#000000";
      this.$emit("change", obj);
    },
  },
};
</script>
<style scoped>
</style>