<!-- 坐标轴边距 -->
<template>
  <Submenu name="18" style="border-bottom: inset 1px">
    <template slot="title"> 坐标轴边距 </template>
    <div class="blockDiv">
      <Row class="ivurow">
        <p class="config-item-title">左边距&nbsp;&nbsp;</p>
        <slider
          v-model="gridOptions.left"
          @on-change="onGridChange"
          style="margin-top: -9px; width: 120px; margin-left: 5px"
        ></slider>
      </Row>
      <Row class="ivurow">
        <p class="config-item-title">顶边距&nbsp;&nbsp;</p>
        <slider
          v-model="gridOptions.top"
          @on-change="onGridChange"
          style="margin-top: -9px; width: 120px; margin-left: 5px"
        ></slider>
      </Row>
      <Row class="ivurow">
        <p class="config-item-title">右边距&nbsp;&nbsp;</p>
        <slider
          v-model="gridOptions.right"
          @on-change="onGridChange"
          style="margin-top: -9px; width: 120px; margin-left: 5px"
        ></slider>
      </Row>
      <Row class="ivurow">
        <p class="config-item-title" style="margin-bottom: 10px">底边距&nbsp;&nbsp;</p>
        <slider
          v-model="gridOptions.bottom"
          @on-change="onGridChange"
          style="margin-top: -9px; width: 120px; margin-left: 5px"
        ></slider>
      </Row>
    </div>
  </Submenu>
</template>
<script>
export default {
  name: "chart-grid-setting",
  props: {
    settings: {
      type: Object,
      required: true,
      default: () => {},
    },
  },
  data() {
    return {
      gridOptions: {},
    };
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.initData();
      },
    },
  },
  methods: {
    initData: function () {
      if (this.settings) {
        this.gridOptions = Object.assign(this.gridOptions, this.settings);
      }
    },
    onGridChange() {
      this.$emit("change", "grid", this.gridOptions);
    },
  },
};
</script>
