<template>
  <div v-cloak>
    <template>
      <!-- 数据编辑 -->
      <Modal
        :loading="loading"
        v-model="addEchart"
        title="数据编辑"
        :width="50"
        @on-ok="addEchartData"
      >
        <div>
          <i-form>
            <form-item>
              <i-input
                type="textarea"
                :autosize="{ minRows: 15, maxRows: 15 }"
                v-model="apiStaticData"
              ></i-input>
            </form-item>
          </i-form>
        </div>
      </Modal>

      <!-- 添加图表 -->
      <chart-type-list
        @on-cancel="selectedChartId = ''"
        @on-ok="setSelectCharType"
        :selectedId="selectedChartId"
        :showModal="chartModule"
      ></chart-type-list>
      <!-- 系列类型信息 -->
      <Modal
        :loading="loading"
        v-model="seriesModal"
        title="系列类型信息"
        :width="30"
        @on-ok="addSeriesType"
        @on-cancel="seriesObj = {}"
      >
        <div style="padding-right: 50px">
          <i-form :model="seriesObj" label-colon :label-width="90">
            <form-item label="系列">
              <i-select
                v-model="seriesObj.name"
                style="width: 100%"
                @on-change="selectMenuList"
                placeholder="请选择"
              >
                <i-option
                  v-for="(item, index) in customColorNameList"
                  :index="index"
                  :key="index"
                  :value="item"
                  >{{ item }}
                </i-option>
              </i-select>
            </form-item>
            <form-item
              label="图表类型"
              v-if="
                selectedChartType !== 'bar.stack' &&
                selectedChartId != 'bar.negative'
              "
            >
              <i-select v-model="seriesObj.type" placeholder="请选择图表类型">
                <i-option value="bar">柱形图</i-option>
                <i-option value="line">折线图</i-option>
              </i-select>
            </form-item>
            <form-item
              label="堆栈类型"
              v-if="
                selectedChartType === 'bar.stack' ||
                selectedChartId === 'bar.negative'
              "
            >
              <i-input
                v-model="seriesObj.stack"
                placeholder="请输入堆栈类型"
              ></i-input>
            </form-item>
          </i-form>
        </div>
      </Modal>

      <!-- 添加表达式 -->
      <Modal
        :loading="loading"
        v-model="customExpressionShow"
        title="添加表达式"
        :width="1000"
        @on-ok="expressionSave"
        @on-cancel="expressionCancel"
        class="expression"
      >
        <i-form label-colon :label-width="90">
          <Row justify="center">
            <i-col>
              <span class="fontColor"
                >请在下面的文本框中输入公式，不需要输入开头的等号:</span
              >
            </i-col>
          </Row>
          <Row justify="center" style="margin-top: 10px">
            <i-col>
              <i-input
                type="textarea"
                v-model="expression"
                placeholder="请输入表达式"
                class="expressionInput"
              ></i-input>
            </i-col>
          </Row>
          <Row justify="center" style="margin-top: 10px">
            <i-col span="6" class="functionDiv">
              <div
                :class="
                  leftFunctionIndex == 0 ? 'leftFunctionSelect' : 'leftFunction'
                "
                @click="leftFunctionClick(0)"
              >
                <span class="fontColor">常用函数</span>
              </div>
            </i-col>
            <i-col span="1"> </i-col>
            <i-col span="13">
              <div class="childrenDiv">
                <div
                  v-if="commonFunction"
                  v-for="(item, index) in newFunctionList"
                  :key="index"
                  @click="rightFunctionClick(item, index)"
                  :class="
                    rightFunctionIndex == index
                      ? 'rightFunctionSelect'
                      : 'activeItem'
                  "
                >
                  <span class="fontColor">{{ item }}</span>
                </div>
              </div>
            </i-col>
            <i-col span="5">
              <function-interpretation-setting
                :text="interpretation"
              ></function-interpretation-setting>
            </i-col>
          </Row>
        </i-form>
      </Modal>
    </template>
    <Card style="width: 100%" v-if="propsContentShow">
      <Tabs size="small" v-model="rightTabName">
        <!-- 基本设置  -->
        <tab-pane label="基本" name="name1" :class="'little-input'">
          <div class="layout-content">
            <div>
              <p>坐标</p>
              <i-input disabled v-model="excel.coordinate"></i-input>
              <p>类型</p>
              <i-select v-model="excel.type">
                <i-option value="text" key="1">文本</i-option>
                <i-option value="pic" key="2">图片</i-option>
                <i-option value="barCode" key="3">条形码</i-option>
                <i-option value="chart" key="4">图表</i-option>
              </i-select>
              <p>值</p>
              <div>
                <i-input
                  v-model="excel.excelValue"
                  @keyup.enter.native="submitValue"
                ></i-input>
              </div>

              <div v-if="excel.hasGroup">
                <p>聚合方式</p>
                <i-select
                  ref="excelPolyWay"
                  :model.sync="excel.polyWay"
                  v-model="excel.polyWay"
                  @on-change="selectPolyList"
                >
                  <i-option v-for="(item, index) in polyWayList" :key="index">{{
                    item.label
                  }}</i-option>
                </i-select>
                <p>扩展方向</p>
                <i-select
                  :model.sync="excel.direction"
                  v-model="excel.direction"
                  @on-change="selectDirectionList"
                >
                  <i-option
                    v-for="(item, index) in directionList"
                    :value="item.value"
                    :key="index"
                    >{{ item.label }}</i-option
                  >
                </i-select>

                <p>高级配置</p>
                <i-select
                  :model.sync="excel.advanced"
                  v-model="excel.advanced"
                  @on-change="selectAdvancedList"
                >
                  <i-option
                    v-for="(item, index) in advancedList"
                    :value="item.value"
                    :key="index"
                    >{{ item.label }}</i-option
                  >
                </i-select>
              </div>

              <p>超链接</p>
              <div>
                <i-input></i-input>
                <Icon type="md-create" />
              </div>

              <p>弹出目标</p>
              <i-select>
                <i-option value="1" key="1">当前窗口</i-option>
                <i-option value="2" key="2">新窗口</i-option>
                <i-option value="3" key="3">父窗口</i-option>
                <i-option value="4" key="4">顶层窗口</i-option>
                <i-option value="5" key="5">自定义</i-option>
              </i-select>

              <p>自定义属性</p>
              <div>
                <i-input></i-input>
              </div>

              <p v-if="isChartSetting">偏移量</p>
              <div v-if="isChartSetting">
                <i-input
                  v-model="offsetInfo"
                  @on-blur="changeLayerOffset"
                ></i-input>
              </div>

              <p>小数位数</p>
              <div>
                <i-input
                  v-model="excel.decimalPlaces"
                  @on-blur="onChangeDecimalPlaces"
                ></i-input>
              </div>
            </div>
          </div>
        </tab-pane>

        <!-- 图表样式设置  -->
        <tab-pane
          v-if="isChartSetting && !backgroundSettingShow"
          label="样式"
          name="name2"
          :disabled="selectedChartType === 'apiUrlType'"
        >
          <i-menu
            theme="light"
            width="auto"
            style="margin-left: -20px"
            :class="menuitemClasses"
            accordion
          >
            <!-- 标题设置 -->
            <chart-title-setting
              v-if="titleSettings"
              @change="onSettingsChange"
              :settings="titleSettings"
            >
            </chart-title-setting>
            <!-- 柱体设置 -->
            <chart-bar-setting
              v-if="barSettings"
              @change="onSeriesChange"
              :settings="barSettings"
              :is-multi-chart="isMultiChart"
            ></chart-bar-setting>
            <!-- 线体设置 -->
            <chart-line-setting
              v-if="lineSettings"
              @change="onSeriesChange"
              :settings="lineSettings"
              :is-multi-chart="isMultiChart"
            ></chart-line-setting>
            <!-- 饼图设置-->
            <chart-pie-setting
              v-if="pieSettings"
              @change="onSeriesChange"
              :settings="pieSettings"
            ></chart-pie-setting>
            <!-- 边距设置-->
            <chart-margin-setting
              v-if="marginSettings"
              @change="onSeriesChange"
              :settings="marginSettings"
            >
            </chart-margin-setting>
            <!-- 中心点设置-->
            <chart-central-point-setting
              v-if="centralPointSettings"
              @change="onSeriesChange"
              :settings="centralPointSettings"
            ></chart-central-point-setting>
            <!-- 漏斗设置-->
            <chart-funnel-setting
              v-if="funnelSettings"
              @change="onSeriesChange"
              :settings="funnelSettings"
            >
            </chart-funnel-setting>
            <!-- 象形图设置 -->
            <chart-pictorial-setting
              v-if="pictorialSettings"
              @change="onPictorialChange"
              @upload-success="pictorialIconUploadSuccess"
              :settings="pictorialSettings"
            ></chart-pictorial-setting>
            <!-- 地图设置 -->
            <chart-map-setting
              ref="mapModal"
              v-if="mapGeoSettings"
              @change="onSettingsChange"
              :settings="mapGeoSettings"
            ></chart-map-setting>
            <!-- 散点设置-->
            <chart-scatter-setting
              v-if="scatterSettings"
              @change="onSeriesChange"
              :settings="scatterSettings"
            >
            </chart-scatter-setting>
            <!-- 雷达设置-->
            <chart-radar-setting
              v-if="radarSettings"
              @change="onSettingsChange"
              :settings="radarSettings"
            >
            </chart-radar-setting>
            <!-- 仪表盘设置-->
            <chart-gauge-setting
              v-if="gaugeSettings"
              @change="onSeriesChange"
              :settings="gaugeSettings"
            >
            </chart-gauge-setting>
            <!-- x轴设置-->
            <chart-xAxis-setting
              v-if="xAxisSettings"
              @change="onSettingsChange"
              :settings="xAxisSettings"
            >
            </chart-xAxis-setting>
            <!-- y轴设置(settings支持数组)-->
            <chart-yAxis-setting
              v-if="yAxisSettings"
              @change="onSettingsChange"
              :settings="yAxisSettings"
            >
            </chart-yAxis-setting>
            <!-- 数值设置-->
            <chart-series-setting
              v-if="seriesLabelSettings"
              @change="onSeriesChange"
              :settings="seriesLabelSettings"
            >
            </chart-series-setting>
            <!-- 提示语设置-->
            <chart-tooltip-setting
              v-if="tooltipSettings"
              @change="onSettingsChange"
              :settings="tooltipSettings"
            >
            </chart-tooltip-setting>
            <!-- 坐标轴边距设置-->
            <chart-grid-setting
              v-if="gridSettings"
              @change="onSettingsChange"
              :settings="gridSettings"
            >
            </chart-grid-setting>
            <!-- 图例设置-->
            <chart-legend-setting
              v-if="legendSettings"
              @change="onSettingsChange"
              :settings="legendSettings"
            >
            </chart-legend-setting>
            <!-- 自定义配色-->
            <chart-match-setting
              style="border-bottom: inset 1px"
              v-if="
                graphSettings ||
                gaugeSettings ||
                funnelSettings ||
                pieSettings ||
                isMultiChart ||
                selectedChartType.indexOf('multi') != -1 ||
                selectedChartType == 'radar.basic'
              "
              :chart-options="chartOptions"
              :data-settings="dataSettings"
            ></chart-match-setting>
            <!-- 背景设置-->
            <chart-background-setting
              @change="chartBackgroundChange"
              @upload-success="chartBackgroundUploadSuccess"
              @remove="removeChartBackground"
              :settings="chartBackground"
            ></chart-background-setting>
          </i-menu>
        </tab-pane>

        <!-- 图表数据设置  -->
        <tab-pane
          v-if="isChartSetting && selectedChartType !== 'map.simple'"
          label="数据"
          name="name3"
          class="little-input"
        >
          <div class="datasource-type">
            <p>数据类型:</p>
            <i-select
              v-model="dataSettings.dataType"
              :disabled="selectedChartType === 'apiUrlType'"
              @on-change="dataTypeChange"
            >
              <i-option value="sql">SQL 数据集</i-option>
              <i-option value="api">API 数据集</i-option>
            </i-select>
          </div>

          <!-- api数据集 -->
          <div class="datasource-type" v-if="dataSettings.dataType == 'api'">
            <div class="datasource-type">
              <p>API 类型:</p>
              <i-select
                v-model="dataSettings.apiStatus"
                :disabled="selectedChartType === 'apiUrlType'"
                @on-change="apiTypeOnChage"
              >
                <i-option value="0">静态数据</i-option>
                <i-option value="1">动态数据</i-option>
                <i-option value="2">接口请求</i-option>
              </i-select>
            </div>
            <div class="datasource-type" v-if="dataSettings.apiStatus == '0'">
              <span
                style="
                  display: inline-block;
                  text-align: left;
                  width: calc(100% - 50px);
                "
                >请自定义数据值:</span
              >
              <i-button
                style="width: 44px"
                size="small"
                type="primary"
                @click="addEchart = true"
                >编辑</i-button
              >
            </div>
            <div class="datasource-type" v-if="dataSettings.apiStatus == '2'">
              <p>接口url:&nbsp;&nbsp;</p>
              <i-input
                v-model="dataSettings.apiUrl"
                :autosize="true"
                type="textarea"
                placeholder="请输入接口地址..."
              >
              </i-input>
            </div>
            <div class="datasource-type" v-if="dataSettings.apiStatus == '1'">
              <div>
                <p>绑定数据集:</p>
                <i-select
                  v-model="dataSettings.dataId"
                  @on-change="onSelectApiDataset"
                >
                  <i-option
                    v-for="(item, index) in apiDatasets"
                    :value="item.dbId"
                    :key="index"
                    >{{ item.title }}</i-option
                  >
                </i-select>
              </div>
            </div>
            <div class="datasource-type" v-if="dataSettings.apiStatus == '1'">
              <p>分类属性:</p>
              <i-select
                v-model="dataSettings.axisX"
                @on-change="onAxisXConfigChange"
              >
                <i-option
                  v-for="(item, index) in apiDatasetFields"
                  :value="item.name"
                  :key="index"
                  >{{ item.title }}</i-option
                >
              </i-select>
            </div>
            <div class="datasource-type" v-if="dataSettings.apiStatus == '1'">
              <p>值属性:</p>
              <i-select
                :model.sync="dataSettings.axisY"
                v-model="dataSettings.axisY"
                @on-change="onAxisYConfigChange"
              >
                <i-option
                  v-for="(item, index) in apiDatasetFields"
                  :value="item.name"
                  :key="index"
                  >{{ item.title }}</i-option
                >
              </i-select>
            </div>
          </div>

          <!-- sql数据集 -->
          <div class="datasource-type" v-if="dataSettings.dataType == 'sql'">
            <div class="datasource-type">
              <p>绑定数据集:</p>
              <i-select
                v-model="dataSettings.dataId"
                @on-change="onSelectSqlDataset"
              >
                <i-option
                  v-for="(item, index) in sqlDatasets"
                  :value="item.dbId"
                  :key="index"
                  >{{ item.title }}</i-option
                >
              </i-select>
            </div>
            <div class="datasource-type">
              <p>分类属性:</p>
              <i-select
                v-model="dataSettings.axisX"
                @on-change="onAxisXConfigChange"
              >
                <i-option
                  v-for="(item, index) in sqlDatasetFields"
                  :value="item.name"
                  :key="index"
                  >{{ item.title }}</i-option
                >
              </i-select>
            </div>
            <div class="datasource-type">
              <p>值属性:</p>
              <i-select
                :model.sync="dataSettings.axisY"
                v-model="dataSettings.axisY"
                @on-change="onAxisYConfigChange"
              >
                <i-option
                  v-for="(item, index) in sqlDatasetFields"
                  :value="item.name"
                  :key="index"
                  >{{ item.title }}</i-option
                >
              </i-select>
            </div>
            <template
              v-if="
                isMultiChart ||
                selectedChartType.indexOf('radar') !== -1 ||
                selectedChartType == 'graph.simple'
              "
            >
              <div class="datasource-type">
                <p>系列属性:</p>
                <i-select v-model="dataSettings.series">
                  <i-option
                    v-for="(item, index) in sqlDatasetFields"
                    :value="item.title"
                    :key="index"
                    >{{ item.title }}</i-option
                  >
                </i-select>
              </div>
            </template>
            <template v-if="selectedChartType == 'graph.simple'">
              <Divider style="margin: 20px 0 20px 0"></Divider>
              <div class="datasource-type">
                <p>绑定节点关系数据集:</p>
                <i-select
                  v-model="dataSettings.dataId1"
                  @on-change="onSelectSqlDataset2"
                >
                  <i-option
                    v-for="(item, index) in sqlDatasets"
                    :value="item.dbId"
                    :key="index"
                    >{{ item.title }}</i-option
                  >
                </i-select>
              </div>
              <div class="datasource-type">
                <p>来源属性:</p>
                <i-select v-model="dataSettings.source">
                  <i-option
                    v-for="(item, index) in sqlDatasetFields2"
                    :value="item.title"
                    :key="index"
                    >{{ item.title }}</i-option
                  >
                </i-select>
              </div>
              <div class="datasource-type">
                <p>目标属性:</p>
                <i-select
                  :model.sync="dataSettings.target"
                  v-model="dataSettings.target"
                >
                  <i-option
                    v-for="(item, index) in sqlDatasetFields2"
                    :value="item.title"
                    :key="index"
                    >{{ item.title }}</i-option
                  >
                </i-select>
              </div>
            </template>
          </div>

          <!-- 多维度处理  -->
          <div
            class="datasource-type"
            v-if="
              ((dataSettings.dataType == 'sql' ||
                dataSettings.apiStatus == '1' ||
                dataSettings.apiStatus == '0') &&
                (selectedChartType == 'mixed.linebar' ||
                  selectedChartType == 'bar.stack')) ||
              selectedChartId == 'bar.negative'
            "
          >
            <p>系列类型:</p>
            <div style="margin-top: 5px">
              <Row class="ivurow" style="margin-top: 5px">
                <i-button
                  type="primary"
                  size="small"
                  @click="seriesModal = true"
                  >新增</i-button
                >
              </Row>
              <i-table
                stripe
                :columns="seriesColumns"
                :data="seriesTypeData"
              ></i-table>
            </div>
          </div>
          <template
            v-if="
              dataSettings.apiStatus === '1' || dataSettings.dataType == 'sql'
            "
          >
            <div class="datasource-type">
              <span
                style="
                  display: inline-block;
                  text-align: left;
                  width: calc(100% - 50px);
                "
                >定时刷新:</span
              >
              <i-switch
                size="small"
                v-model="dataSettings.isTiming"
                @on-change="timerChange"
              />
            </div>
            <div
              class="datasource-type"
              style="display: flex; align-items: center"
              v-if="dataSettings.isTiming"
            >
              <span
                style="
                  display: inline-block;
                  text-align: left;
                  width: calc(100% - 100px);
                "
                >刷新间隔:</span
              >
              <i-input
                size="small"
                type="number"
                v-model="dataSettings.intervalTime"
                style="width: 100px"
                @on-blur="timerChange"
                ><span slot="append">秒</span></i-input
              >
            </div>
          </template>
          <i-button
            @click="runChart"
            type="primary"
            style="width: 100%; height: 36px; margin-top: 10%"
            >运行
          </i-button>
        </tab-pane>

        <!-- 背景图设置  -->
        <tab-pane
          v-if="backgroundSettingShow"
          style="visibility: visible"
          label="背景图设置"
          name="name4"
          :class="'little-input'"
        >
          <div
            :class="backgroundSettings.path ? 'excel-backgroud-st' : ''"
            style="height: 500px; overflow-y: auto"
          >
            <span style="display: inline-block; margin: 5px 0">图片：</span>
            <Upload
              ref="upload"
              :show-upload-list="false"
              :default-file-list="backgroundImg"
              :on-success="backgroundImgUploadSuccess"
              :on-exceeded-size="(e) => handleMaxSize(e, 10)"
              :format="['jpg', 'jpeg', 'png']"
              :max-size="10240"
              :action="actionUrlPre + '/jmreport/upload'"
              style="display: inline-block; width: 58px"
            >
              <div
                style="display: block"
                class="pictorial-icon-upload"
                v-if="backgroundSettings.path"
              >
                <img
                  style="width: 196px; max-height: 100px"
                  :src="getBackgroundImg()"
                />
                <div class="cover" style="width: 196px">
                  <Icon type="ios-create-outline" />
                </div>
              </div>
              <i-button
                v-else
                style="margin-left: 25px"
                type="primary"
                size="small"
                >上传</i-button
              >
            </Upload>

            <div style="width: 100%">
              <p style="padding: 6px 0">图片宽度:</p>
              <i-input
                v-model="backgroundSettings.width"
                @on-blur="backgroundChange"
              ></i-input>

              <p style="padding: 6px 0">图片高度:</p>
              <i-input
                v-model="backgroundSettings.height"
                @on-blur="backgroundChange"
              ></i-input>
            </div>

            <div style="width: 100%">
              <p style="padding: 6px 0">重复设置:</p>
              <i-select
                v-model="backgroundSettings.repeat"
                style="width: 99%"
                @on-change="backgroundChange"
              >
                <i-option value="repeat">默认</i-option>
                <i-option value="repeat-x">水平重复</i-option>
                <i-option value="repeat-y">垂直重复</i-option>
                <i-option value="no-repeat">无重复</i-option>
              </i-select>
            </div>

            <i-button
              v-if="backgroundSettings.path"
              style="width: 99%; margin: 10px 0"
              type="primary"
              @click="removeBackground"
              >取消背景图</i-button
            >
          </div>
        </tab-pane>

        <!-- 条形码设置  -->
        <tab-pane
          v-if="barcodeSettings"
          style="visibility: visible"
          label="条形码设置"
          name="name5"
          :class="'little-input'"
        >
          <bar-code-setting
            @change="onBarcodeChange"
            :settings="barcodeSettings"
          ></bar-code-setting>
        </tab-pane>

        <!-- 二维码设置  -->
        <tab-pane
          v-if="qrcodeSettings"
          style="visibility: visible"
          label="二维码设置"
          name="name6"
          :class="'little-input'"
        >
          <qr-code-setting
            @change="onBarcodeChange"
            :settings="qrcodeSettings"
          ></qr-code-setting>
        </tab-pane>
      </Tabs>
