    centerDivClass: function () {
      let str = "chart-designer";
      if (!this.dataShow && !this.propsContentShow) {
        str += " all";
      } else if (!this.dataShow) {
        str += " left";
      } else if (!this.propsContentShow) {
        str += " right";
