  methods: {
    // 自定义系列类型
    addSeriesType() {
      let obj = {
        ...this.seriesObj,
      };
      if (obj._index >= 0) {
        this.seriesTypeData.splice(obj._index, 1, obj);
      } else {
        this.seriesTypeData.push(obj);
      }
      //运行到图表
      this.runChart();
      this.seriesObj = {
        name: "",
        type: "",
      };
      this.seriesModal = false;
    },
    showChartConfig: function (key, value) {
      this.selectChart({
        type: "chart",
        extData: "",
        chartType: key,
        options: value,
      });
    },
    // 设置选择图表类型
    setSelectCharType(item) {
      newtecRequest({
        operServiceId: "chartConfigService",
        operId: "getEchartDefaultConfig",
        data: item.id,
        success: (res) => {
          const { status, data, error } = res;
          if (status == 0) {
            this.selectedChartType = item.type;
            this.selectedChartId = item.id;
            const options = JSON.parse(data);

            addChartPreHandler.call(this, options);
            options.backgroundColor = "#fff";

            exportApi.addChart(options, null, (result) => {
              this.selectChart(result);
              exportApi.updateChartExtData(result.id, {
                chartId: item.id,
                chartType: item.type,
              });
            });

            this.chartModule = false;
            this.specialChartType = "";
            //判断图表类型
            this.isMultiChart = options.series.length > 1 ? true : false;
            this.isChartSetting = true;
          } else {
            this.$Message.info(error || "图表数据加载失败");
          }
        },
      });
    },
    submitValue(e) {
      let value = e.target.value;
      setExcelData(
        {
          ri: this.excel.ri,
          ci: this.excel.ci,
        },
        value
      );
    },
    selectMenuList(val) {
      this.designerObj.type = val;
    },
    savePopup() {
      var that = this;
      let creatFlag = that.excelQueryName();
      if (creatFlag) {
        let data = that.dataXs;
        const saveFn = (obj) => {
          // 判断报表是否有分组字段
          const rows = data.rows;
          let groupField = "";
          for (let key in rows) {
            if (groupField) break;
            const cells = rows[key].cells;
            for (let cellKey in cells) {
              const text = cells[cellKey].text;
              if (!text) continue;
              if (text.includes(".group(")) {
                groupField = text
                  .replace("group(", "")
                  .replace(")}", "")
                  .replace("#{", "");
                break;
              }
            }
          }
          if (groupField) {
            data.isGroup = true;
            data.groupField = groupField;
          }
          if (data.chartList && data.chartList.length > 0) {
            data.chartList.forEach((item) => {
              if (item.width === 0 || item.height === 0) {
                item.width = "650";
                item.height = "350";
              }
            });
          }
          var dataStr = JSON.stringify({
            designerObj: obj,
            ...data,
          });
          if (obj.name != null && obj.name != "") {
            $http.post({
              contentType: "json",
              url: api.saveReport,
              data: dataStr,
              success: (result) => {
                exportApi.tip("保存成功!");
                onbeforeunload = "return true";
                window.location.reload();
              },
            });
          }
        };
        that.handleCreate(saveFn, data);
        that.visible = false;
      }
    },
    handleCreate(cb, data) {
      var that = this;
      var obj = that.designerObj;
      if (obj.name == "" || obj.name == null) {
        that.visible = true;
        that.dataXs = data;
        //that.$Message.error("名称不能为空!");
        return "";
      }
      //同步校验
      let creatFlag = that.excelQueryName();
      if (creatFlag) {
        obj.id = excel_config_id;
        cb(obj);
      }
    },
    excelQueryName() {
      var creatFlag = true;
      var that = this;
      var obj = that.designerObj;
      if (obj.name != "" && obj.name != null) {
        $http.post({
          contentType: "json",
          url: api.excelQueryName,
          data: JSON.stringify(obj),
          fail: (result) => {
            obj.name = "";
            creatFlag = false;
          },
        });
      }
      return creatFlag;
    },
    onChangeDecimalPlaces(e) {
      exportApi.cellProp(this.excel.ri, this.excel.ci, {
        decimalPlaces: e.target.value,
      });
    },
    selectPolyList(name) {
      exportApi.getExcelData((excelData) => {
        //下拉改变单元格值
        let data = excelData.getSelectArea();
        if (data.sri < 0 || data.sci < 0) return;
        if (!excelData.rows["_"][data.sri]) return;
        if (!excelData.rows["_"][data.sri].cells[data.sci]) return;
        let textr = excelData.rows["_"][data.sri].cells[data.sci].text;
        if (textr != "" && textr.indexOf("#{") != -1) {
          if (name === "group") {
            if (
              excelData.rows["_"][data.sri].cells[data.sci].direction ===
              "right"
            )
              return;
            excelData.rows["_"][data.sri].cells[data.sci].aggregate = "group";
            let text = excelData.rows["_"][data.sri].cells[data.sci].text;
            let newxsdata = text.replace(
              subStringStr(text, "#{", "}").split(".")[1],
              "group(" + subStringStr(text, "#{", "}").split(".")[1] + ")"
            );
            xsSetNewdata(data, newxsdata);
          } else if (name === "select") {
            if (
              excelData.rows["_"][data.sri].cells[data.sci].direction ===
              "right"
            )
              return;
            excelData.rows["_"][data.sri].cells[data.sci].aggregate = "select";
            let text = excelData.rows["_"][data.sri].cells[data.sci].text;
            let subtext = subStringStr(text, "#{", "}").split(".")[1];
            let newxsdata = text.replace(
              subtext,
              subStringStr(subtext, "group(", ")")
            );
            xsSetNewdata(data, newxsdata);
          }
        }
      });
    },
    selectDirectionList(name) {
      exportApi.getExcelData((excelData) => {
        //下拉改变单元格值
        let data = excelData.getSelectArea();
        if (data.sri < 0 || data.sci < 0) return;
        if (!excelData.rows["_"][data.sri]) return;
        if (!excelData.rows["_"][data.sri].cells[data.sci]) return;
        let textr = excelData.rows["_"][data.sri].cells[data.sci].text;
        if (textr != "" && textr.indexOf("#{") != -1) {
          if (name === "right") {
            excelData.rows["_"][data.sri].cells[data.sci].direction = "right";
            let text = excelData.rows["_"][data.sri].cells[data.sci].text;
            let text2 = subStringStr(text, "#{", "}").split(".")[1];
            let text4 = "";
            if (text2.indexOf("group(") != -1) {
              text4 = subStringStr(text2, "group(", ")");
            } else {
              text4 = text2;
            }
            let text3 = "groupRight(" + text4 + ")";
            let newxsdata = text.replace(text2, text3);
            xsSetNewdata(data, newxsdata);
            this.excel.polyWay = "group";
            this.$refs.excelPolyWay.disabled = true;
          } else if (name === "down") {
            excelData.rows["_"][data.sri].cells[data.sci].direction = "down";
            let text = excelData.rows["_"][data.sri].cells[data.sci].text;
            let subtext = subStringStr(text, "#{", "}").split(".")[1];
            let newxsdata = text.replace(
              subtext,
              subStringStr(subtext, "groupRight(", ")")
            );
            xsSetNewdata(data, newxsdata);
            this.excel.polyWay = "select";
            this.$refs.excelPolyWay.disabled = false;
          }
        }
      });
    },
    selectAdvancedList(name) {
      exportApi.getExcelData((excelData) => {
        //下拉改变单元格值
        let data = excelData.getSelectArea();
        if (data.sri < 0 || data.sci < 0) return;
        if (!excelData.rows["_"][data.sri]) return;
        if (!excelData.rows["_"][data.sri].cells[data.sci]) return;
        let textr = excelData.rows["_"][data.sri].cells[data.sci].text;
        if (textr != "" && textr.indexOf("#{") != -1) {
          if (name === "dynamic") {
            this.excel.advanced = "dynamic";
            if (
              excelData.rows["_"][data.sri].cells[data.sci].aggregate ===
              "dynamic"
            )
              return;
            excelData.rows["_"][data.sri].cells[data.sci].aggregate = "dynamic";
            let text = excelData.rows["_"][data.sri].cells[data.sci].text;
            text = text.replace("eachTitle(", "").replace(")", "");
            let newxsdata = text.replace(
              subStringStr(text, "#{", "}").split(".")[1],
              "dynamic(" + subStringStr(text, "#{", "}").split(".")[1] + ")"
            );
            xsSetNewdata(data, newxsdata);
          } else if (name === "title") {
            this.excel.advanced = "title";
            if (
              excelData.rows["_"][data.sri].cells[data.sci].aggregate ===
              "eachTitle"
            )
              return;
            excelData.rows["_"][data.sri].cells[data.sci].aggregate =
              "eachTitle";
            let text = excelData.rows["_"][data.sri].cells[data.sci].text;
            text = text.replace("dynamic(", "").replace(")", "");
            let newxsdata = text.replace(
              subStringStr(text, "#{", "}").split(".")[1],
              "eachTitle(" + subStringStr(text, "#{", "}").split(".")[1] + ")"
            );
            xsSetNewdata(data, newxsdata);
          } else if (name === "default") {
            this.excel.advanced = "default";
            excelData.rows["_"][data.sri].cells[data.sci].aggregate = "";
            let text = excelData.rows["_"][data.sri].cells[data.sci].text;
            text = text
              .replace("dynamic(", "")
              .replace(")", "")
              .replace("eachTitle(", "")
              .replace(")", "");
            let newxsdata = text.replace(
              subStringStr(text, "#{", "}").split(".")[1],
              subStringStr(text, "#{", "}").split(".")[1]
            );
            xsSetNewdata(data, newxsdata);
          }
        }
      });
    },
    // 是否使用字典下拉框改变事件
    selectUseDict(value) {
      //下拉改变单元格值
      if (!value && value != 0) {
        return;
      }
      exportApi.getExcelData((excelData) => {
        let data = excelData.getSelectArea();
        if (data.sri < 0 || data.sci < 0) {
          return;
        }
        if (value == 0) {
          let code = this.excel.dictCode;
          this.removeReportDictCode(code);
          this.excel.dictCode = "";
          excelData.rows["_"][data.sri].cells[data.sci]["dictCode"] = "";
        }
        excelData.rows["_"][data.sri].cells[data.sci]["isDict"] = value;
        //xs.sheet.reload()
      });
    },
    // 字典编码改变事件
    changeDictCode() {
      exportApi.getExcelData((excelData) => {
        let value = this.excel.dictCode;
        let data = excelData.getSelectArea();
        if (data.sri < 0 || data.sci < 0) return;
        if (this.excel.isDict == 1) {
          excelData.rows["_"][data.sri].cells[data.sci]["dictCode"] = value;
          this.addReportDictCode(value);
        }
      });
    },
    // 新增字典编码到excel data dicts
    addReportDictCode(code) {
      exportApi.getExcelData((excelData) => {
        if (!excelData.dicts) {
          excelData.dicts = [];
        }
        excelData.dicts.push(code);
      });
    },
    // 移除字典编码
    removeReportDictCode(code) {
      exportApi.getExcelData((excelData) => {
        if (excelData.dicts) {
          let index = excelData.dicts.indexOf(code);
          if (index >= 0) {
            excelData.dicts.splice(index, 1);
          }
        }
      });
    },
    // 渲染button
    renderColorButton(h, params, key, type) {
      if (key == "action") {
        return h(
          "div",
          {
            style: {
              display: "flex",
            },
          },
          [
            h("i-button", {
              props: Object.assign(
