                  type: "text",
                },
                {
                  icon: "md-create",
                }
              ),
              on: {
                click: () => {
                  this.seriesObj = params.row;
                  this.seriesModal = true;
                },
              },
            }),
            h("i-button", {
              props: Object.assign(
                {},
                {
                  size: "small",
                  type: "text",
                },
                {
