    // 图表标题
    echTilChange(status) {
      this.echartInfo.titleShow = status;
    },
    addEchartData() {
      this.addEchart = false;
      let str = this.apiStaticData;
      let settings = Object.assign(this.dataSettings, {
        run: 1,
        axisX: "name",
        axisY: "value",
        series: "type",
      });
      refreshChart.call(
        this,
        this.echartInfo.id,
