    // 选择图表sql
    selectSqlListOpt(name) {
      this.dataAllocation.sqlxAxis = "";
      this.dataAllocation.sqlseries = "";
      this.dataAllocation.sqlgroup = "";
      this.dataAllocation.sqltype = "";
      this.dataAllocation.seriesTypeData = [];
      this.dataAllocation.sqlListOpt.forEach((item) => {
        if (item.dbId === name) {
          this.dataAllocation.sqlListField = item.children;
        }
      });
    },
    changeLayerOffset() {
      let value = this.offsetInfo;
      if (!value) {
        exportApi.updateLayerOffset(this.echartInfo.id, [0, 0]);
        return;
      }
      if (value.indexOf(",") < 0) {
        this.$Message.warning("偏移量格式不正确！");
        return;
      }
      let arr = value.split(",");
      if (arr.length != 2) {
        this.$Message.warning("偏移量格式不正确！");
        return;
      }
      exportApi.updateLayerOffset(this.echartInfo.id, arr);
    },
    // 单击选中图表
    selectChart(data) {
      if (!data) {
        console.error("选中图层但是未回调任何信息!");
        return;
      }
      this.echartInfo.id = data.id;
      if (!data.offset) {
        this.offsetInfo = "0,0";
      } else {
        this.offsetInfo = data.offset.join(",");
      }
      if (data.type == "barcode") {
        //条形码走这个逻辑
        this.barcodeSettings = JSON.parse(data.jsonString);
        this.rightTabName = "name5";
      } else if (data.type == "qrcode") {
        this.qrcodeSettings = JSON.parse(data.jsonString);
        this.rightTabName = "name6";
      } else {
        // 图表点击走这个逻辑
        this.selectedChartType = data["chartType"];
        this.selectedChartId = data["chartId"];
        this.chartOptions = "";
        if (!data.options) {
          return;
        }
        this.rightTabName = "name2";
        this.isChartSetting = true;
        if (this.selectedChartType === "apiUrlType") {
          this.dataSettings.dataType = "api";
          this.dataSettings.apiStatus = "2";
          this.dataSettings.apiUrl = data.extData["apiUrl"];
          return;
        }
        handleChartOptions.call(this, data.options);
        handleChartExtData.call(this, data.extData);
      }
    },
    styleChanges(key) {
      if (!key) {
        this.echDataRun();
      } else {
      }
    },
    /**
     * 背景图重复设置改变事件
     * @param value
     */
    backgroundChange() {
      exportApi.setBackground(this.backgroundSettings);
    },
    /**
     * 消除右侧tabpane的显示属性
     */
    clearRightTabpane() {
      this.barcodeSettings = false;
      this.qrcodeSettings = false;
      this.backgroundSettingShow = false;
      this.isChartSetting = false;
    },
    // 条形码/二维码设置改变事件
    onBarcodeChange(settings) {
      let id = this.echartInfo.id;
      exportApi.updateChart(id, settings);
    },
    onClickCell() {
      this.rightTabName = "name1";
      this.clearRightTabpane();
    },
    // 鼠标右键背景图设置 事件
    handleBackground(param) {
      this.clearRightTabpane();
      Object.keys(this.backgroundSettings).map((k) => {
        this.backgroundSettings[k] = param[k] ? param[k] : "";
      });
      this.rightTabName = "name4";
      this.backgroundSettingShow = true;
    },
    removeBackground() {
      Object.keys(this.backgroundSettings).map((k) => {
        this.backgroundSettings[k] = "";
      });
      exportApi.setBackground({
        path: false,
      });
    },
    // 文件上传成功，修改 excel 背景图
    backgroundImgUploadSuccess(res) {
      if (!res.success) {
        this.$Message.warning(res.message);
      } else {
        let path = res.message;
        if (path.indexOf("http") < 0) {
          if (path.indexOf("/") != 0) {
            path = "/" + path;
          }
        }
        this.backgroundSettings["path"] = path;
        if (!this.backgroundSettings["repeat"]) {
          this.backgroundSettings["repeat"] = "repeat";
        }
        exportApi.setBackground(this.backgroundSettings);
      }
    },
    // 图表背景图上传成功
    chartBackgroundUploadSuccess(res, chartBackground, callback) {
      if (!res.success) {
        this.$Message.warning(res.message);
      } else {
        let path = res.message;
        if (path.indexOf("http") < 0) {
          if (path.indexOf("/") != 0) {
            path = "/" + path;
          }
        }
        this.chartBackground["image"] = path;
        callback && callback(path);
        this.chartBackgroundChange(chartBackground);
      }
    },
    // 设置图表背景色
    chartBackgroundChange(chartBackground) {
      if (chartBackground["enabled"]) {
        // 启用设置
        let imageSrc = chartBackground["image"];
        let color = chartBackground["color"];
        if (imageSrc) {
          this.chartOptions["backgroundColor"] = {
            src: imageSrc,
          };
        } else if (color) {
          this.chartOptions["backgroundColor"] = color;
        } else {
          if (this.chartOptions["backgroundColor"]) {
            delete this.chartOptions["backgroundColor"];
          }
        }
      } else {
        if (this.chartOptions["backgroundColor"]) {
          delete this.chartOptions["backgroundColor"];
        }
      }
      //TODO 图标背景图有个坑 项目地址切换导致图片显示不了
      let id = this.echartInfo.id;
      exportApi.updateChart(id, this.chartOptions);
    },
    // 获取图片预览图
    getBackgroundImg() {
      let path = this.backgroundSettings["path"];
      if (path) {
        if (path.indexOf("http") < 0) {
          path = baseFull + path;
        }
      }
      return path;
    },
    // 获取图片预览图
    getChartBackgroundImg() {
      let path = this.chartBackground["image"];
      if (path) {
        if (path.indexOf("http") < 0) {
          path = baseFull + path;
        }
      }
      return path;
    },
    // 删除背景图意味着要重置背景
    removeChartBackground(chartBackground) {
      this.chartBackgroundChange(chartBackground);
    },
    // 图片上传文件大小
    handleMaxSize(file, size) {
      this.$Notice.warning({
        title: "超出文件大小限制",
        desc: "文件  " + file.name + " 太大，请上传" + size + "M以内图片",
        duration: 6,
      });
    },
    // 象形图图标上传回调
    pictorialIconUploadSuccess(res, callback) {
      if (!res.success) {
        this.$Message.warning(res.message);
      } else {
        let symbol = "image://";
        if (res.message.indexOf("http") == 0) {
          symbol += res.message;
        } else {
          symbol += window.location.origin + baseFull + "/" + res.message;
        }
        this.pictorialSettings["symbol"] = symbol;
        callback && callback(symbol);
        for (let item of this.chartOptions["series"]) {
          item["symbol"] = symbol;
        }
        let id = this.echartInfo.id;
        exportApi.updateChart(id, this.chartOptions);
      }
    },
    getPathBySymbol() {
      let symbol = this.pictorialSettings["symbol"];
      if (!symbol) {
        let path =
          baseFull + "/jmreport/desreport_/chartsImg/pictorialIcon/spirits.png";
        return path;
      } else {
        return symbol.replace("image://", "");
      }
    },
    onAxisYConfigChange(value) {
      let fields =
        this.dataSettings.dataType == "sql"
          ? this.sqlDatasetFields
          : this.apiDatasetFields;
      for (let field of fields) {
        if (field["title"] == value) {
          this.dataSettings["yText"] = field["remark"];
          break;
        }
      }
    },
    onAxisXConfigChange(value) {
      let fields =
        this.dataSettings.dataType == "sql"
          ? this.sqlDatasetFields
          : this.apiDatasetFields;
      for (let field of fields) {
        if (field["title"] == value) {
          this.dataSettings["xText"] = field["remark"];
          break;
        }
      }
    },
    timerChange() {
      // 时间的变换，更新dataSetting
      let id = this.echartInfo.id;
      exportApi.updateChartExtData(id, this.dataSettings);
    },
    onLegendChange() {
      this.onSettingsChange("legend", this.legendSettings);
    },
    onTooltipChange() {
      this.onSettingsChange("tooltip", this.tooltipSettings);
    },
    onGridChange() {
      this.onSettingsChange("grid", this.gridSettings);
    },
    onDoubleYAxisChange() {
      this.onSettingsChange("yAxis", this.doubleyAxisSettings);
    },
    onGeoChange() {
      this.onSettingsChange("geo", this.mapGeoSettings);
    },
    onGraphChange() {
      this.onSeriesChange(this.graphSettings);
    },
    onPictorialChange(pictorialSettings) {
      let settings = pictorialSettings;
      let arr = this.chartOptions["series"];
      let oneSeries = arr[0];
      let option = util.setting2Option(pictorialSettings);
      Object.assign(oneSeries, option);
      /*if(!oneSeries['symbol']){
          oneSeries['symbol'] = base64_spirits
      }*/
      this.chartOptions["xAxis"]["max"] = option["symbolBoundingData"];
      let newSeries = [];
      newSeries.push(oneSeries);
      if (settings["double"] == true) {
        let itemStyle = {
          normal: {
            opacity: settings["secondOpacity"],
          },
        };
        let twoSeries = Object.assign({}, oneSeries, {
          symbolClip: false,
          itemStyle: itemStyle,
        });
        newSeries.push(twoSeries);
      }
      this.chartOptions["series"] = newSeries;
      let id = this.echartInfo.id;
      exportApi.updateChart(id, this.chartOptions);
    },
    onSeriesLabelChange() {
      this.onSeriesChange(this.seriesLabelSettings, "label");
    },
    // series更新事件
    onSeriesChange(whatSetting, propName) {
      let option = {};
      if (!propName) {
        option = util.setting2Option(whatSetting);
      } else {
        option[propName] = util.setting2Option(whatSetting);
      }
      let arr = this.chartOptions["series"];
      for (let i = 0; i < arr.length; i++) {
        let item = arr[i];
        let temp = JSON.parse(JSON.stringify(option));
        //颜色 走自定义的设定
        if (this.isMultiChart === true && temp["itemStyle"]) {
          temp["itemStyle"]["color"] = item["itemStyle"]["color"];
        }
        if (
          this.isMultiChart === false &&
          item["type"] === "line" &&
          temp["isArea"] === false
        ) {
          //设置面积折线
          temp["areaStyle"] = null;
        }
        Object.assign(item, temp);
      }
      if (arr[0].type === "gauge") {
        let c1 = this.colorMatchData[0]
          ? this.colorMatchData[0].color
          : "#91c7ae";
        let c2 = this.colorMatchData[1]
          ? this.colorMatchData[1].color
          : "#63869E";
        let c3 = this.colorMatchData[2]
          ? this.colorMatchData[2].color
          : "#C23531";
        let arr = [
          [0.2, c1],
          [0.8, c2],
          [1, c3],
        ];
        this.chartOptions["series"][0]["axisLine"]["lineStyle"].color = arr;
      }
      //雷达图中心点设置刷新
      if (arr[0].type === "radar") {
        this.chartOptions["radar"][0][
          "center"
        ] = this.centralPointSettings.center;
      }
      if (arr[0].type === "pie") {
        this.chartOptions["series"][0][
          "center"
        ] = this.centralPointSettings.center;
      }
      let id = this.echartInfo.id;
      exportApi.updateChart(id, this.chartOptions);
    },
    onSettingsChange(optionKey, whatSetting) {
      let option = null;
      if (typeJudge(whatSetting, "Array")) {
        option = util.setting2Array(whatSetting);
      } else {
        option = util.setting2Option(whatSetting);
      }
      this.chartOptions[optionKey] = option;
      let id = this.echartInfo.id;
      // 地图注册
      if (this.chartOptions["geo"]) {
        let map = this.$refs.mapModal.allMapList.filter(
          (item) => item.name === this.chartOptions["geo"]["map"]
        );
        if (map[0]) {
          exportApi.registerMap(this.chartOptions["geo"]["map"], map[0].data);
        }
      }
      exportApi.updateChart(id, this.chartOptions);
    },
    /**
     * 图表sql数据集发生改变时
     */
    onSelectSqlDataset() {
      const dataId = this.dataSettings["dataId"];
      Object.keys(this.dataSettings).map((k) => {
        this.dataSettings[k] = "";
      });
      this.dataSettings["dataType"] = "sql";
      this.dataSettings["dataId"] = dataId;
      this.onDatasetIdChange(dataId);
    },
    onSelectSqlDataset2() {
      const dataId1 = this.dataSettings["dataId1"];
      this.dataSettings["dataId1"] = dataId1;
      this.loadDasetField(dataId1, (options) => {
        this.sqlDatasetFields2 = options;
      });
    },
    onDatasetIdChange(dataId) {
      this.loadDasetField(dataId, (options) => {
        this[`${this.dataSettings.dataType}DatasetFields`] = options;
      });
    },

    /**
     * @param {string} dataId 数据集ID
     * @param {(filedOptions) => {}} callback 回调方法
     * 加载SQL数据集字段信息
     */
    loadDasetField(dataId, callback) {
      this.seriesTypeData = [];
      const that = this,
        type = this.dataSettings.dataType;
      if (type == "api" || type == "sql") {
        newtecRequest({
          operServiceId: `${type}DatasetService`,
          operId: "queryDatasetInfo",
          data: parseInt(dataId),
          success: (res) => {
            const { status, data } = res;
            const options = [];
            if (status == 0 && Array.isArray(data.fields)) {
              for (let item of data.fields) {
                options.push({
                  title: item.remark,
                  name: item.name,
                });
              }
            }
            callback && callback(options);
          },
        });
      }
    },
    /**
     * @param {string} dataId 数据集ID
     * @param {(sqlData) => {}} callback 回调方法
     * 加载SQL数据集数据
     */
    loadSqlDatasetData(dataId, callback) {
      newtecRequest({
        operServiceId: "sqlDatasetService",
        operId: "queryDatasetData",
        data: dataId,
        success: (res) => {
          const { status, data } = res;
          if (status == 0 && data) {
            callback && callback(data);
          }
        },
      });
    },
    onSelectApiDataset(value) {
      this.customColorNameList = [];
      let arr = this.apiDatasets.filter((item) => {
        return item.dbId === value;
      });
      this.onDatasetIdChange(value);
      if (arr.length > 0) {
        this.dataSettings["dbCode"] = arr[0].code;
        this.dataSettings["apiUrl"] = arr[0].apiUrl;
        let id = this.echartInfo.id;
        this.dataSettings["chartType"] = this.selectedChartType;
        this.dataSettings["chartId"] = this.selectedChartId;
        this.dataSettings["run"] = 1;
        // this.runByDataSettings(id, this.chartOptions, this.dataSettings);
      }
    },
    runChart() {
      let id = this.echartInfo.id;
      this.dataSettings["chartType"] = this.selectedChartType;
      this.dataSettings["chartId"] = this.selectedChartId;
      this.dataSettings["run"] = 1;
      this.runByDataSettings(id, this.chartOptions, this.dataSettings);
    },
    /**
     * 刷新图表
     * @param chartId 图表id
     * @param options 图表options
     * @param settings 图表extData
     */
    runByDataSettings(chartId, options, settings) {
      let dataId = settings.dataId;
      const that = this;
      if (settings.dataType === "sql") {
        this.loadSqlDatasetData(dataId, (sqlData) => {
          // 如果是关系图
          if (settings["chartType"] === "graph.simple") {
            this.loadSqlDatasetData(settings.dataId1, (res) => {
              let { source, target } = settings;
              let links = res.map((item, index) => {
                return {
                  source: item[source],
                  target: item[target],
                };
              });
              refreshChart.call(this, chartId, options, settings, {
                data: sqlData,
                links: links,
              });
            });
          } else {
            refreshChart.call(this, chartId, options, settings, sqlData);
          }
        });
      } else if (settings.dataType === "api") {
        // API数据集
        Object.assign(settings, {
          axisX: settings.axisX,
          axisY: settings.axisY,
          series: settings.series,
        });
        if (settings.apiStatus == "0") {
          // 静态数据
          refreshChart.call(
            this,
            chartId,
            options,
            settings,
            JSON.parse(that.apiStaticData)
          );
        } else if (settings.apiStatus == "1") {
          newtecRequest({
            operServiceId: "apiDatasetService",
            operId: "queryDatasetData",
            data: dataId,
            success: (res) => {
              const { status, data } = res;
              if (status == 0) {
                // let result = settings["chartType"] === "graph.simple" ? res : res.data;
                refreshChart.call(this, chartId, options, settings, data);
              }
            },
          });
        } else if (settings.apiStatus == "2") {
          httpGet({
            url: settings.apiUrl,
            success: (res) => {
              if (res.option) {
                // 判断请求到的 json 的数据类型
                getSelectType.call(that, res.option);
                let chartExtData = Object.assign(settings, {
                  dataId: chartId,
                  chartType: that.selectedChartType,
                  chartId: this.selectedChartId,
                });
                exportApi.updateChart(chartId, res.option);
                //更新ExtData
                exportApi.updateChartExtData(chartId, chartExtData);
                if (that.selectedChartType !== "apiUrlType") {
                  //绑定样式
                  handleChartOptions.call(that, res.option);
                }
              }
            },
          });
        }
      }
    },
    // 将配置运行为图表
    echDataRun() {
      let that = this;
      let id = that.echartInfo.id;
      if (!that.dataAllocation.selectOptionData.series) return;
      let charType = that.dataAllocation.selectOptionData.series[0].type;
      //动态api数据获取
      if (
        that.dataAllocation.dataType === "sqlechData" &&
        that.dataAllocation.selectId != ""
      ) {
        let sqlxAxis = that.dataAllocation.sqlxAxis;
        let sqlseries = that.dataAllocation.sqlseries;
        let sqlgroup = that.dataAllocation.sqlgroup;
        let sqltype = that.dataAllocation.sqltype;
        let seriesTypeData = that.dataAllocation.seriesTypeData;
        $jm.qurestSql(that.dataAllocation, (result) => {
          let backObj = querySqlData(
            charType,
            result,
            sqlxAxis,
            sqlseries,
            sqlgroup,
            seriesTypeData,
            that.dataAllocation.optionData
          );
          that.dataAllocation.optionData = JSON.stringify(backObj);
        });
      } else if (
        that.dataAllocation.dataType === "apiechData" &&
        that.dataAllocation.selectId != ""
      ) {
        if (that.dataAllocation.status == "dynamicData") {
          $jm.qurestApi(that.dataAllocation, function (res) {
            let backObj = queryApiData(
              charType,
              res,
              that.dataAllocation.optionData
            );
            that.dataAllocation.optionData = JSON.stringify(backObj);
          });
        }
      }
      //静态数据组装json
      let options = config2Json(
        that.echartInfo,
        that.dataAllocation.optionData,
        this.dataAllocation.selectOptionData
      );
      //通过id修改配置
      let chartExtData = {};
      chartExtData.dataType = that.dataAllocation.dataType;
      chartExtData.status = that.dataAllocation.status;
      chartExtData.selectId = that.dataAllocation.selectId;
      chartExtData.sqlxAxis = that.dataAllocation.sqlxAxis;
      chartExtData.sqlseries = that.dataAllocation.sqlseries;
      chartExtData.sqlgroup = that.dataAllocation.sqlgroup;
      chartExtData.sqltype = that.dataAllocation.sqltype;
      chartExtData.seriesTypeData = that.dataAllocation.seriesTypeData;
      exportApi.updateChart(id, options);
      exportApi.updateChartExtData(id, chartExtData);
      let data = {};
      data.id = id;
      data.options = options;
      data.extData = chartExtData;
      this.selectChart(data);
    },
    // 打开图表选择弹框
    addChartModule() {
      this.selectedChartType = "";
      this.selectedChartId = "";
      this.chartModule = true;
    },
    dataTypeChange: function () {
      this.seriesTypeData = [];
      const that = this,
        type = this.dataSettings.dataType;
      if (type == "api" || type == "sql") {
        newtecRequest({
          operServiceId: `${type}DatasetService`,
          operId: "getDatasetIdAndName",
          data: exportApi.reportId,
          success: (res) => {
            const { status, data } = res;
            const options = [];
            if (status == 0) {
              for (let item of data) {
                options.push({
                  title: `${item.name}(${item.id})`,
                  dbId: item.id,
                });
              }
              that[`${type}Datasets`] = options;
            }
          },
        });
      }
    },
    // 页面有图表时显示样式和数据tab
    tabPaneShow() {
      this.isChartSetting = true;
    },
    // 进入设计页面或刷新时运行所有图表
    refreshAllChart(chartList) {
      let that = this;
      for (let item of chartList) {
        let id = item.layer_id;
        let options = JSON.parse(item.config);
        that.runByDataSettings(id, options, item.extData);
      }
    },
    // 添加表达式点击事件
    customExpression() {
      this.customExpressionShow = true;
    },
    // 左侧函数点击事件
    leftFunctionClick(index) {
      let functionListElement = this.functionList[index];
      this.newFunctionList = functionListElement.name.split(",");
      this.leftFunctionIndex = index;
      this.commonFunction = true;
    },
    // 右侧函数点击事件
    rightFunctionClick(val, index) {
      this.expression = "=" + val + "()";
      this.interpretation = val;
      this.rightFunctionIndex = index;
    },
    // 表达式保存
    expressionSave() {
      exportApi.setSelectCellExpress(this.expression);
      this.expressionCancel();
    },
    // 表达式取消
    expressionCancel() {
      this.expression = "";
      this.commonFunction = false;
      this.customExpressionShow = false;
      this.loading = false;
      this.leftFunctionIndex = -1;
      this.rightFunctionIndex = -1;
    },
    /**
     * API 类型改变事件
     * @param {number} value API 类型
     */
    apiTypeOnChage(value) {
      if (value != 2) {
        this.seriesTypeData = [];
      }
    },
    // 添加字典点击事件
    createDictClick() {
      this.$refs.dataDictionary.dictShow = true;
    },
  },
};
</script>
<style>
.page-container {
  height: 100%;
}
.ivu-card,
.ivu-card-body {
  height: 100%;
}
</style>