<!-- 雷达图设置 -->
<template>
  <Submenu name="10" style="border-bottom: inset 1px">
    <template slot="title"> 雷达图设置 </template>
    <div class="blockDiv">
      <Row
        class="ivurow"
        v-for="(item, index) in radarOption[0].indicator"
        :key="index"
      >
        <Tooltip :content="item.name" placement="bottom">
          <p>{{ item.name.substr(0, 2) }}..最大值&nbsp;&nbsp;</p>
        </Tooltip>
        <i-input
          size="small"
          type="number"
          v-model="item.max"
          @on-blur="onRadarChange"
          style="width: 81px"
        ></i-input>
      </Row>
      <Row class="ivurow">
        <p class="config-item-title">字体大小&nbsp;&nbsp;</p>
        <i-input
          size="small"
          type="number"
          v-model="radarOption[0].name_textStyle_fontSize"
          @on-blur="onRadarChange"
          style="width: 111px"
        ></i-input>
      </Row>
      <Row
        class="ivurow"
        v-if="typeof radarOption[0].name_textStyle_color !== 'undefined'"
      >
        <p class="config-item-title">字体颜色&nbsp;&nbsp;</p>
        <i-input
          v-model="radarOption[0].name_textStyle_color"
          size="small"
          style="width: 111px"
          @on-change="onRadarChange"
        >
          <span slot="append">
            <color-picker
              class="colorPicker"
              v-model="radarOption[0].name_textStyle_color"
              :editable="false"
              :transfer="true"
              size="small"
              @on-change="onRadarChange"
            />
          </span>
        </i-input>
      </Row>

      <Row
        class="ivurow"
        v-if="typeof radarOption[0].axisLine_lineStyle_color !== 'undefined'"
      >
        <p class="config-item-title">轴线颜色&nbsp;&nbsp;</p>
        <i-input
          v-model="radarOption[0].axisLine_lineStyle_color"
          size="small"
          style="width: 111px"
          @on-change="onRadarChange"
        >
          <span slot="append">
            <color-picker
              class="colorPicker"
              v-model="radarOption[0].axisLine_lineStyle_color"
              :editable="false"
              :transfer="true"
              size="small"
              @on-change="onRadarChange"
            />
          </span>
        </i-input>
      </Row>
    </div>
  </Submenu>
</template>
<script>
export default {
  name: "chart-radar-setting",
  props: {
    settings: {
      type: Array,
      required: true,
      default: () => {},
    },
  },
  data() {
    return {
      radarOption: [],
    };
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.initData();
      },
    },
  },
  methods: {
    initData: function () {
      if (this.settings) {
        this.radarOption = Object.assign(this.radarOption, this.settings);
      }
    },
    onRadarChange() {
      this.$emit("change", "radar", this.radarOption);
    },
  },
};
</script>
<style scoped>
</style>