<!-- 中心点设置 -->
<template>
  <Submenu name="5" style="border-bottom: inset 1px">
    <template slot="title"> 中心点设置 </template>
    <div class="blockDiv" style="padding-bottom: 10px">
      <Row class="ivurow">
        <p class="config-item-title">x轴&nbsp;&nbsp;</p>
        <i-input
          size="small"
          type="number"
          v-model="centralPoint.center[0]"
          @on-change="onMarginChange"
          style="width: 120px; margin-left: 5px"
          :value="centralPoint.center[0]"
        ></i-input>
      </Row>
      <Row class="ivurow">
        <p style="margin-bottom: 10px">y轴&nbsp;&nbsp;</p>
        <i-input
          size="small"
          type="number"
          v-model="centralPoint.center[1]"
          @on-change="onMarginChange"
          style="width: 120px; margin-left: 5px"
          :value="centralPoint.center[1]"
        ></i-input>
      </Row>
    </div>
  </Submenu>
</template>
<script>
export default {
  name: "chart-central-point-setting",
  props: {
    settings: {
      type: Object,
      required: true,
      default: () => {},
    },
  },
  data() {
    return {
      centralPoint: {
        center: []
      },
    };
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.initData();
      },
    },
  },
  methods: {
    initData: function () {
      if (this.settings) {
        this.centralPoint = Object.assign(this.centralPoint, this.settings);
      }
    },
    onMarginChange() {
      this.$emit("change", this.centralPoint);
    },
  },
};
</script>
