<!-- 柱体设置 -->
<template>
  <Submenu name="2" style="border-bottom: inset 1px">
    <template slot="title"> 柱体设置 </template>
    <div class="blockDiv">
      <Row class="ivurow">
        <p class="config-item-title">宽度&nbsp;&nbsp;</p>
        <slider
          v-model="barOptions.barWidth"
          @on-change="onBarChange"
          style="margin-top: -9px; width: 120px; margin-left: 5px"
        ></slider>
      </Row>
      <Row class="ivurow">
        <p class="config-item-title">圆角&nbsp;&nbsp;</p>
        <slider
          v-model="barOptions.itemStyle_barBorderRadius"
          @on-change="onBarChange"
          style="margin-top: -9px; width: 120px; margin-left: 5px"
        ></slider>
      </Row>
      <Row class="ivurow">
        <p class="config-item-title" style="margin-bottom: 10px">最小高度&nbsp;&nbsp;</p>
        <slider
          v-model="barOptions.barMinHeight"
          @on-change="onBarChange"
          style="margin-top: -9px; width: 120px; margin-left: 5px"
        ></slider>
      </Row>
      <Row
        v-if="
          isMultiChart === false &&
          typeof barOptions.itemStyle_color !== 'undefined'
        "
        class="ivurow"
      >
        <p class="config-item-title">柱体颜色&nbsp;&nbsp;</p>
        <i-input
          v-model="barOptions.itemStyle_color"
          size="small"
          style="width: 111px"
          @on-change="onBarChange"
        >
          <span slot="append">
            <color-picker
              class="colorPicker"
              v-model="barOptions.itemStyle_color"
              :editable="false"
              alpha
              :transfer="true"
              size="small"
              @on-change="onBarChange"
            />
          </span>
        </i-input>
      </Row>
      <Row
        v-if="
          isMultiChart === false &&
          typeof barOptions.backgroundStyle_color !== 'undefined'
        "
        class="ivurow"
      >
        <p class="config-item-title">柱体背景&nbsp;&nbsp;</p>
        <i-input
          v-model="barOptions.backgroundStyle_color"
          size="small"
          style="width: 111px"
          @on-change="onBarChange"
        >
          <span slot="append">
            <color-picker
              class="colorPicker"
              v-model="barOptions.backgroundStyle_color"
              :editable="false"
              alpha
              :transfer="true"
              size="small"
              @on-change="onBarChange"
            />
          </span>
        </i-input>
      </Row>
    </div>
  </Submenu>
</template>
<script>
export default {
  name: "chart-bar-setting",
  props: {
    settings: {
      type: Object,
      required: true,
      default: () => {},
    },
    isMultiChart: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      barOptions: {
        barWidth: 50,
        itemStyle_barBorderRadius: 0,
        itemStyle_color: "#c43632",
        barMinHeight: 2,
        textStyle_color: "black",
        textStyle_fontWeight: "bolder",
      },
    };
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.initData();
      },
    },
  },
  methods: {
    initData: function () {
      if (this.settings) {
        this.barOptions = Object.assign(this.barOptions, this.settings);
      }
    },
    onBarChange() {
      this.$emit("change", this.barOptions);
    },
  },
};
</script>
