import './index.less';
import Vue from 'vue'
import exportApi from "@/utils/exportApi";
import ChartSetting from '@/components/ChartSetting'
import {
  api
} from "@/utils/api";

// 所有需要用到的组件
const components = [
  ChartSetting
]

/**
 * 定义初始化方法 必须调用
 * @param {Object} Vue 
 * @param {Object} [options={}] 
 */
const install = function (Vue, options = {}) {
  if (!install.componentInstalled) {
    // 注册组件
    components.map(component => {
      Vue.component(component.name, component)
    })
    install.componentInstalled = true
  }
}

install(Vue);

const newtecChart = {
  init,
  exportApi
}

/**
 * 初始化图表配置栏
 * @param {HTMLElement} outDom 
 * @param {Object} lang 语言
 */
function init(outDom, options = {}, lang) {

  // 初始化 API 信息
  const apiOptions = options.api;
  if(apiOptions && typeof apiOptions === 'object') {
    for(let key in apiOptions) {
      api[key] = apiOptions[key];
    }
  }

  let dom = document.createElement('div')
  dom.id = 'report-chart'
  outDom.appendChild(dom);
  new Vue({
    el: '#report-chart',
    data() {
      return {
        lang
      }
    },
    mounted: function() {
      const chartSetting = this.$refs.chartSetting;
      newtecChart.loadChart = (options) => {
        chartSetting.selectChart(options);
      }
      newtecChart.selectChart = () => {
        chartSetting.addChartModule();
      }
      newtecChart.onClickCell = (cell) => {
        chartSetting.onClickCell(cell);
      }
    },
    template: `<chart-setting ref='chartSetting'></chart-setting>`
  });
}

if(!window.newtecChart) {
  window.newtecChart = newtecChart
}

export default newtecChart;