<!-- Y轴设置 -->
<template>
  <div>
    <Submenu
      :name="'yAxis' + index"
      style="border-bottom: inset 1px"
      v-for="(item, index) in yAxisOptions"
      :key="index"
    >
      <template slot="title"> {{ getTitle(index) }}Y轴设置 </template>
      <div class="blockDiv" style="padding-bottom: 10px">
        <Row class="ivurow">
          <p class="config-item-title">显示&nbsp;&nbsp;</p>
          <i-switch
            size="small"
            style="margin-left: 105px"
            v-model="item.show"
            @on-change="onYAxisChange"
          />
        </Row>
        <Row class="ivurow">
          <p class="config-item-title">Y轴名称&nbsp;&nbsp;</p>
          <i-input
            size="small"
            v-model="item.name"
            @on-blur="onYAxisChange"
            style="width: 111px"
          ></i-input>
        </Row>
        <Row class="ivurow">
          <p class="config-item-title">分隔线&nbsp;&nbsp;</p>
          <i-switch
            size="small"
            style="margin-left: 90px"
            v-model="item.splitLine_show"
            @on-change="onYAxisChange"
          />
        </Row>
        <Row class="ivurow" v-if="typeof item.splitLine_show !== 'undefined'">
          <p class="config-item-title">颜色设置&nbsp;&nbsp;</p>
          <Col>
            <i-input
              v-model="item.splitLine_lineStyle_color"
              size="small"
              style="width: 111px"
              @on-change="onYAxisChange"
            >
              <span slot="append">
                <color-picker
                  class="colorPicker"
                  v-model="item.splitLine_lineStyle_color"
                  :editable="false"
                  :transfer="true"
                  size="small"
                  @on-change="onYAxisChange"
                />
              </span>
            </i-input>
          </Col>
        </Row>
        <Row class="ivurow">
          <p class="config-item-title" style="margin-bottom: 10px">字体大小&nbsp;&nbsp;</p>
          <i-input
            size="small"
            type="number"
            v-model="item.axisLabel_textStyle_fontSize"
            @on-blur="onYAxisChange"
            style="width: 111px"
          ></i-input>
        </Row>
        <Row
          class="ivurow"
          v-if="typeof item.axisLabel_textStyle_color !== 'undefined'"
        >
          <p class="config-item-title">字体颜色&nbsp;&nbsp;</p>
          <Col>
            <i-input
              v-model="item.axisLabel_textStyle_color"
              size="small"
              style="width: 111px"
              @on-change="onYAxisChange"
            >
              <span slot="append">
                <color-picker
                  class="colorPicker"
                  v-model="item.axisLabel_textStyle_color"
                  :editable="false"
                  :transfer="true"
                  size="small"
                  @on-change="onYAxisChange"
                />
              </span>
            </i-input>
          </Col>
        </Row>
        <Row
          class="ivurow"
          v-if="typeof item.axisLine_lineStyle_color !== 'undefined'"
        >
          <p class="config-item-title">轴线颜色&nbsp;&nbsp;</p>
          <Col>
            <i-input
              v-model="item.axisLine_lineStyle_color"
              size="small"
              style="width: 111px"
              @on-change="onYAxisChange"
            >
              <span slot="append">
                <color-picker
                  class="colorPicker"
                  v-model="item.axisLine_lineStyle_color"
                  :editable="false"
                  :transfer="true"
                  size="small"
                  @on-change="onYAxisChange"
                />
              </span>
            </i-input>
          </Col>
        </Row>
      </div>
    </Submenu>
  </div>
</template>
<script>
export default {
  name: "chart-yAxis-setting",
  props: {
    settings: {
      type: [Object, Array],
      required: true,
      default: null,
    },
    dataIndex: 0,
  },
  data() {
    return {
      yAxisOption: {},
      yAxisOptions: [],
    };
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.initData();
      },
    },
  },
  methods: {
    getTitle: function (index) {
      if (this.settings instanceof Array) {
        if (index == 0) {
          return "左";
        }
        if (index == 1) {
          return "右";
        }
      } else {
        return "";
      }
    },
    initData: function () {
      if (this.settings) {
        if (this.settings instanceof Array) {
          this.yAxisOptions = this.settings;
          this.yAxisOption = Object.assign(
            this.yAxisOption,
            this.settings[this.dataIndex]
          );
        } else {
          this.yAxisOptions = [];
          this.yAxisOptions.push(this.settings);
          this.yAxisOption = Object.assign(this.yAxisOption, this.settings);
        }
      }
    },
    onYAxisChange() {
      if (this.settings instanceof Array) {
        this.$emit("change", "yAxis", this.yAxisOptions);
      } else {
        this.$emit("change", "yAxis", this.yAxisOptions[0]);
      }
    },
  },
};
</script>
