<template>
  <div style="height: auto; overflow: hidden; padding-bottom: 200px">
    <i-form label-position="top" class="newtec-form-barcode">
      <form-item label="条形码内容:">
        <i-input
          v-model="barcodeContent"
          type="textarea"
          :rows="2"
          @on-blur="onBarcodeChange"
        ></i-input>
      </form-item>

      <form-item label="条间距:">
        <i-input
          v-model="width"
          placeholder="请输入条间距"
          :min="1"
          type="number"
          @on-change="onBarcodeChange"
        ></i-input>
      </form-item>

      <form-item label="高度:">
        <i-input
          v-model="height"
          placeholder="请输入高度"
          :min="1"
          type="number"
          @on-change="onBarcodeChange"
        ></i-input>
      </form-item>

      <form-item label="条颜色:">
        <i-input
          v-model="lineColor"
          size="small"
          style="width: 111px"
          @on-change="onBarcodeChange"
        >
          <span slot="append">
            <color-picker
              class="colorPicker"
              v-model="lineColor"
              :editable="false"
              alpha
              :transfer="true"
              size="small"
              @on-change="onBarcodeChange"
            />
          </span>
        </i-input>
      </form-item>

      <form-item label="背景色:">
        <i-input
          v-model="background"
          size="small"
          style="width: 111px"
          @on-change="onBarcodeChange"
        >
          <span slot="append">
            <color-picker
              class="colorPicker"
              v-model="background"
              :editable="false"
              alpha
              :transfer="true"
              size="small"
              @on-change="onBarcodeChange"
            />
          </span>
        </i-input>
      </form-item>

      <form-item label="是否显示文字:" class="newtec-form-horizontal">
        <div>
          <i-switch v-model="displayValue" @on-change="onBarcodeChange" />
        </div>
      </form-item>

      <template v-if="displayValue">
        <form-item label="覆盖文字:">
          <i-input
            v-model="text"
            placeholder="请输入覆盖文字"
            @on-blur="onBarcodeChange"
          ></i-input>
        </form-item>

        <form-item label="文字位置:">
          <i-select
            v-model="textPosition"
            placeholder="请选择文字位置"
            @on-change="onBarcodeChange"
          >
            <i-option value="bottom">底部</i-option>
            <i-option value="top">上方</i-option>
          </i-select>
        </form-item>

        <form-item label="文字水平对齐:">
          <i-select
            v-model="textAlign"
            placeholder="请选择水平对齐方式"
            @on-change="onBarcodeChange"
          >
            <i-option value="center">居中</i-option>
            <i-option value="left">左对齐</i-option>
            <i-option value="right">右对齐</i-option>
          </i-select>
        </form-item>

        <form-item label="文字大小:">
          <i-input
            v-model="fontSize"
            placeholder="请输入文字大小"
            :min="1"
            type="number"
            @on-change="onBarcodeChange"
          ></i-input>
        </form-item>

        <form-item label="文字样式:">
          <i-select
            v-model="fontOptions"
            placeholder="请选择文字样式"
            @on-change="onBarcodeChange"
          >
            <i-option value="bold">加粗</i-option>
            <i-option value="italic">斜体</i-option>
            <i-option value="bold italic">加粗&斜体</i-option>
          </i-select>
        </form-item>
      </template>
    </i-form>
  </div>
</template>
<script>
export default {
  name: 'bar-code-setting',
  props: {
    settings: {
      type: Object,
      required: true,
      default: () => {
        return {
          barcodeContent: "广州粤信科技有限公司",
          background: "#fff",
          lineColor: "#000",
          width: "",
          height: "",
          displayValue: false,
          text: "",
          textPosition: "bottom",
          textAlign: "center",
          fontSize: "",
          fontOptions: "",
        };
      },
    },
  },
  data() {
    return {};
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.resetForm();
      },
    },
  },
  methods: {
    validateBarcode: (value) => {
      let reg = /^[a-zA-Z0-9]+$/;
      let reg2 = /^.{4,18}$/;
      // 长度为4到18个字符
      if (value !== "" && !reg.test(value)) {
        Vue.prototype.$Message.error("条码内容只允许字母、数字");
        return false;
      }
      return true;
    },
    resetForm: function () {
      if (this.settings) {
        Object.keys(this.settings).map((k) => {
          this[k] = this.settings[k];
        });
      }
    },
    onBarcodeChange() {
      if (this.validateBarcode(this.barcodeContent)) {
        let obj = {};
        Object.keys(this.settings).map((k) => {
          obj[k] = this[k];
        });
        //清除颜色后恢复默认
        obj.background = this.background ? obj.background : "#ffffff";
        obj.lineColor = this.lineColor ? obj.lineColor : "#000000";
        this.$emit("change", obj);
      }
    },
  },
};
</script>
<style scoped>
.newtec-form-barcode .ivu-form-item-label {
  padding-bottom: 2px !important;
}
.newtec-form-barcode .ivu-switch:after {
  height: 16px;
}

.newtec-form-barcode .ivu-switch {
  height: 20px;
  line-height: 20px;
}
.newtec-form-barcode .ivu-color-picker .ivu-select-dropdown {
  left: -142px !important;
}
.newtec-form-horizontal .ivu-form-item-content {
  margin-left: 100px;
}

.newtec-form-horizontal .ivu-form-item-label {
  text-align: left;
  width: 100px;
  float: left;
  padding: 10px 12px 10px 0;
}
</style>