<!-- 散点设置 -->
<template>
  <Submenu name="9" style="border-bottom: inset 1px">
    <template slot="title"> 散点设置 </template>
    <div class="blockDiv" style="padding-bottom: 10px">
      <Row class="ivurow">
        <p class="config-item-title">大小&nbsp;&nbsp;</p>
        <slider
          v-model="scatterOption.symbolSize"
          @on-change="onScatterChange"
          style="margin-top: -9px; width: 120px; margin-left: 5px"
        ></slider>
      </Row>
      <Row
        class="ivurow"
        v-if="typeof scatterOption.itemStyle_color !== 'undefined'"
      >
        <p class="config-item-title">颜色&nbsp;&nbsp;</p>
        <Col>
          <i-input
            v-model="scatterOption.itemStyle_color"
            size="small"
            style="width: 111px"
            @on-change="onScatterChange"
          >
            <span slot="append">
              <color-picker
                class="colorPicker"
                v-model="scatterOption.itemStyle_color"
                :editable="false"
                alpha
                :transfer="true"
                size="small"
                @on-change="onScatterChange"
              />
            </span>
          </i-input>
        </Col>
      </Row>
      <Row class="ivurow">
        <p class="config-item-title" style="margin-bottom: 10px">透明度&nbsp;&nbsp;</p>
        <slider
          :max="1"
          :step="0.1"
          v-model="scatterOption.itemStyle_opacity"
          @on-change="onScatterChange"
          style="margin-top: -9px; width: 110px; margin-left: 5px"
        ></slider>
      </Row>
    </div>
  </Submenu>
</template>
<script>
export default {
  name: "chart-scatter-setting",
  props: {
    settings: {
      type: Object,
      required: true,
      default: () => {},
    },
  },
  data() {
    return {
      scatterOption: {
        itemStyle_color: "#C23531",
      },
    };
  },
  watch: {
    settings: {
      deep: true,
      immediate: true,
      handler: function () {
        this.initData();
      },
    },
  },
  methods: {
    initData: function () {
      if (this.settings) {
        this.scatterOption = Object.assign(this.scatterOption, this.settings);
      }
    },
    onScatterChange() {
      this.$emit("change", this.scatterOption);
    },
  },
};
</script>
<style scoped>
</style>